/*

 */
import {
  reqAddress,
  reqCategorys,
  reqShops
} from '../../api'
import {
  RECEIVE_ADDRESS,
  RECEIVE_SHOPS,
  RECEIVE_CATEGORYS
} from '../mutation-types'

const state = {
  latitude: 40.10038, // 
  longitude: 116.36867, // 
  address: {}, // 
  categorys: [], // 
  shops: [], // 
}

const mutations = {
  // 
  [RECEIVE_ADDRESS] (state, address) { // state
    state.address = address
  },
  // 
  [RECEIVE_SHOPS] (state, shops) {
    state.shops = shops
  },
  // 
  [RECEIVE_CATEGORYS] (state, categorys) {
    state.categorys = categorys
  },

  abc (state) {
    console.log('msite mutation abc()', state)
  }
}

const actions = {
  // action
  async getAddress ({commit, state}) {
    const {longitude, latitude} = state
    // 1. ajax
    const result = await reqAddress(longitude, latitude)
    // 2. , mutation
    if(result.code===0) {
      const addess = result.data
      commit(RECEIVE_ADDRESS, addess)
    }

  },

  // action
  async getCategorys ({commit}, callback) {
    // 1. ajax
    const result = await reqCategorys()
    // 2. , mutation
    if(result.code===0) {
      const categorys = result.data
      commit(RECEIVE_CATEGORYS, categorys)
      // 
      typeof callback ==='function' && callback()
    }

  },

  // action
  async getShops ({commit, state}) {
    const {longitude, latitude} = state
    // 1. ajax
    const result = await reqShops({longitude, latitude})
    // 2. , mutation
    if(result.code===0) {
      const shops = result.data
      commit(RECEIVE_SHOPS, shops)
    }
  },

  yyy ({commit, state}) {// state
    console.log('msite action yyy()', state)
    commit('abc') // mutation()
  }

}

const getters = {
  totalShopCount (state) {
    return state.shops.length
  }
}

export default {
  state,
  mutations,
  actions,
  getters,
}


