/*

 */
import Vue from 'vue'

import {
  RECEIVE_INFO,
  RECEIVE_RATINGS,
  RECEIVE_GOODS,
  INCREMENT_FOOD_COUNT,
  DECREMENT_FOOD_COUNT,
  CLEAR_CART
} from '../mutation-types'

import {
  reqGoods,
  reqRatings,
  reqInfo
} from '../../api'

const state = {
  goods: [], // 
  ratings: [], // 
  info: {}, // 
  cartFoods2: [], // food
}

const mutations = {
  [RECEIVE_INFO] (state, {info}) {
    state.info = info
  },

  [RECEIVE_RATINGS](state, {ratings}) {
    state.ratings = ratings
  },

  [RECEIVE_GOODS](state, {goods}) {
    state.goods = goods
  },

  [INCREMENT_FOOD_COUNT](state, {food}) {
    if(food.count) {
      food.count++
    } else { // +
      /*
      : (), 
      : Vue.set(), 
       */
      // food: count: 1
      // food.count = 1

      // 
      Vue.set( food, 'count', 1)

      // food
      state.cartFoods2.push(food)
    }
  },

  [DECREMENT_FOOD_COUNT](state, {food}) {
    if(food.count>0) {
      food.count--

      if(food.count===0) {
        // food
        state.cartFoods2.splice(state.cartFoods2.indexOf(food), 1)
      }
    }
  },

  [CLEAR_CART](state) {
    // foodcount
    state.cartFoods2.forEach(food => food.count = 0)
    // 
    state.cartFoods2 = []
  },

  abc (state) {
    console.log('shop mutation abc()', state)
  }
}

const actions = {
  // 
  async getInfo({commit}) {
    const result = await reqInfo()
    if(result.code===0) {
      const info = result.data
      commit(RECEIVE_INFO, {info})
    }
  },

  // 
  async getRatings({commit}) {
    const result = await reqRatings()
    if(result.code===0) {
      const ratings = result.data
      commit(RECEIVE_RATINGS, {ratings})
    }
  },

  // 
  async getGoods({commit}) {
    const result = await reqGoods()
    if(result.code===0) {
      const goods = result.data
      commit(RECEIVE_GOODS, {goods})
    }
  },

  // foodcount
  updateFoodCount ({commit}, {food, isAdd}) {
    if(isAdd) {
      commit(INCREMENT_FOOD_COUNT, {food})
    } else {
      commit(DECREMENT_FOOD_COUNT, {food})
    }
  },

  yyy ({commit, state}) {// state
    console.log('shop action yyy()', state)
    commit('abc') // mutation()
  }
}

const getters = {
  // food
  cartFoods (state) {
    let arr = []

    state.goods.forEach(good => {
      good.foods.forEach(food => {
        if(food.count>0) {
          arr.push(food)
        }
      })
    })

    return arr
  },

  // food
  totalCount (state) {
    return state.cartFoods2.reduce((pre, food) => pre + food.count, 0)
  },

  // food
  totalPrice (state) {
    return state.cartFoods2.reduce((pre, food) => pre + food.count * food.price, 0)
  },

  // 
  ratingsTotalCount (state) {
    return state.ratings.length
  },

  // 
  positiveRatingsTotalCount (state) {
    return state.ratings.reduce((pre, rating) => pre + (rating.rateType===0 ? 1 : 0), 0)
  }
}

export default {
  state,
  mutations,
  actions,
  getters,
}


