<template>
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo"></h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginType}" @click="loginType=true"></a>
          <a href="javascript:;" :class="{on: !loginType}" @click="loginType=false"></a>
        </div>
      </div>
      <div class="login_content">
        <form>
          <div :class="{on: loginType}">
            <section class="login_message">
              <!--<input type="tel" maxlength="11" placeholder=""
                     name="phone" v-model="phone" v-validate="{required: true, regex: /^1\d{10}$/}">-->
              <input type="tel" maxlength="11" placeholder=""
                     name="phone" v-model="phone" v-validate="'required|mobile'">
              <span style="color: red;">{{errors.first('phone')}}</span>
              <button :disabled="!isRightPhone || computeTime>0" class="get_verification"
                      :class="{right_phone_number: isRightPhone}"
                      @click.prevent="sendCode">
                {{computeTime>0 ? `(${computeTime}s)` : ''}}
              </button>
            </section>
            <section class="login_verification">
              <input type="tel" maxlength="8" placeholder="" v-model="code"
                     name="code" v-validate="{required: true, regex: /^\d{6}$/}">
              <span style="color: red;">{{errors.first('code')}}</span>
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>

          <div :class="{on: !loginType}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="name"
                       name="" v-validate="'required'">
                <span style="color: red;">{{errors.first('')}}</span>
              </section>
              <section class="login_verification">
                <input :type="isShowPwd ? 'text' : 'password'" placeholder="" v-model="pwd"
                       name="" v-validate="'required'">
                <div class="switch_button" :class="isShowPwd ? 'on' : 'off'" @click="isShowPwd = !isShowPwd">
                  <div class="switch_circle" :class="{right: isShowPwd}"></div>
                  <span class="switch_text">{{isShowPwd ? 'abc' : ''}}</span>
                </div>

                <span style="color: red;">{{errors.first('')}}</span>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha"
                       name="" v-validate="{required: true, regex: /^.{4}$/}">
                <img class="get_verification" src="http://localhost:5000/captcha" alt="captcha"
                  @click="updateCaptcha" ref="captcha">
                <span style="color: red;">{{errors.first('')}}</span>
              </section>
            </section>
          </div>

          <button class="login_submit" @click.prevent="login"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont icon-jiantou2"></i>
      </a>
    </div>
  </section>
</template>
<script>
  import { Toast, MessageBox } from 'mint-ui'
  import {
    reqSendCode,
    reqPwdLogin,
    reqSmsLogin
  } from '../../api'
  import {RECEIVE_USER} from '../../store/mutation-types'
  export default {
    name: 'Login',
    data () {
      return {
        loginType: false, // : true-, false-
        phone: '', // 
        code: '', // 
        name: '', // 
        pwd: '', // 
        captcha: '', // 
        computeTime: 0,// 
        isShowPwd: false, // 
      }
    },

    computed: {
      // 
      isRightPhone () {
        return /^1\d{10}$/.test(this.phone)
      }
    },

    methods: {
      // 
      async sendCode () {
        // alert('----')
        // 
        this.computeTime = 30
        // 1s1
        const intervalId = setInterval(() => {
          this.computeTime--
          // 0, 
          if(this.computeTime<=0) {
            clearInterval(intervalId)
          }
        }, 1000)

        // ajax, 
        const result = await reqSendCode(this.phone)
        if(result.code===0) {
          // alert('')
          Toast('')
        } else {
          // 
          this.computeTime = 0
          // alert(result.msg)
          MessageBox.alert(result.msg)
        }
      },

      // 
      async login () {

        let names
        let result
        const {loginType, phone, code, name, pwd, captcha} = this
        if(loginType) {
          names = ['phone', 'code']
        } else {
          names = ['', '', '']
        }

        const success = await this.$validator.validateAll(names)
        if(success) {
          // , 
          if(loginType) {
            result = await reqSmsLogin(phone, code)
          } else {
            result = await reqPwdLogin({name, pwd, captcha})
          }
          // , 
          if(result.code===0) { // 
            const user = result.data
            // userstate
            this.$store.commit(RECEIVE_USER, user)
            // 
            this.$router.replace('/profile')
          } else { // 
            MessageBox.alert(result.msg)
          }

        }
      },

      // 
      updateCaptcha () {
        // imgsrc, ()
        this.$refs.captcha.src = 'http://localhost:5000/captcha?time=' + Date.now()
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixins.styl"

  .loginContainer
    width 100%
    height 100%
    background #fff
    .loginInner
      padding-top 60px
      width 80%
      margin 0 auto
      .login_header
        .login_logo
          font-size 40px
          font-weight bold
          color #02a774
          text-align center
        .login_header_title
          padding-top 40px
          text-align center
          >a
            color #333
            font-size 14px
            padding-bottom 4px
            &:first-child
              margin-right 40px
            &.on
              color #02a774
              font-weight 700
              border-bottom 2px solid #02a774
      .login_content
        >form
          >div
            display none
            &.on
              display block
            input
              width 100%
              height 100%
              padding-left 10px
              box-sizing border-box
              border 1px solid #ddd
              border-radius 4px
              outline 0
              font 400 14px Arial
              &:focus
                border 1px solid #02a774
            .login_message
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .get_verification
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                border 0
                color #ccc
                font-size 14px
                background transparent
                &.right_phone_number
                  color: black
            .login_verification
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .switch_button
                font-size 12px
                border 1px solid #ddd
                border-radius 8px
                transition background-color .3s,border-color .3s
                padding 0 6px
                width 30px
                height 16px
                line-height 16px
                color #fff
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                &.off
                  background #fff
                  .switch_text
                    float right
                    color #ddd
                &.on
                  background #02a774
                >.switch_circle
                //transform translateX(27px)
                  position absolute
                  top -1px
                  left -1px
                  width 16px
                  height 16px
                  border 1px solid #ddd
                  border-radius 50%
                  background #fff
                  box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                  transition transform .3s
                  &.right
                    transform translateX(27px)
            .login_hint
              margin-top 12px
              color #999
              font-size 14px
              line-height 20px
              >a
                color #02a774
          .login_submit
            display block
            width 100%
            height 42px
            margin-top 30px
            border-radius 4px
            background #4cd96f
            color #fff
            text-align center
            font-size 16px
            line-height 42px
            border 0
        .about_us
          display block
          font-size 12px
          margin-top 20px
          text-align center
          color #999
      .go_back
        position absolute
        top 5px
        left 5px
        width 30px
        height 30px
        >.iconfont
          font-size 20px
          color #999
</style>


